module.exports = " <div id=app> <h1> Vue-Toast </h1> <p> A Vue component show tip to user(just like Android system's toast). </p><p> </p><p> It will automatically disappear in some seconds :) </p> <p> <button @click=showAbove>Click it be above</button> <vue-toast v-ref:tabove message=\"I am here\" position=\"top middle\" animate=scale></vue-toast> </p> <p> <button @click=showBelow>Click it be below</button> <vue-toast v-ref:tbelow message=\"I am here\" position=\"bottom middle\" animate=opacity></vue-toast> </p> <p> <a href=https://github.com/frehaiku/vue-toast target=_blank>Fork me on Github!</a> </p> </div> ";


